import { Component, OnInit } from '@angular/core';
import { ObservableMedia, MediaChange } from '@angular/flex-layout';
import { Subscription } from 'rxjs/Subscription';
import { ObserableService } from './obserable.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  // tslint:disable-next-line:no-inferrable-types
  private oppen: boolean = true;
  // tslint:disable-next-line:no-inferrable-types
  private over: string = 'side';
  private watcher: Subscription;
  // tslint:disable-next-line:no-inferrable-types
  private themeClassName: string = '';
  // tslint:disable-next-line:no-inferrable-types
  private nowMedia: string = '';

  constructor(private media: ObservableMedia, private obsService: ObserableService) {
    this.watcher = this.media.subscribe((change: MediaChange) => {
      this.nowMedia = change.mqAlias;
      if (this.nowMedia === 'sm' || this.nowMedia === 'xs') {
        this.oppen = false;
        this.over = 'over';
      } else {
        this.oppen = true;
        this.over = 'side';
      }
    });
  }

  ngOnInit() {
    this.getCookie();
    this.obsService.leftMenuOppen.subscribe((open: boolean) => {
      if (this.nowMedia === 'sm' || this.nowMedia === 'xs') {
        this.oppen = open;
      }
    });
  }

  getThemeName(themeClassName: string) {
    this.themeClassName = themeClassName;
    this.setCookie();
  }

  toGetOppen(oppen: boolean) {
    this.oppen = oppen;
  }

  getCookie() {
    if (document.cookie === '') {
      this.themeClassName = 'custom-theme-light';
      this.setCookie();
    } else {
      this.themeClassName = document.cookie.split('theme=')[1];
    }
  }

  setCookie() {
    const expire_days = 30;
    const d = new Date();
    d.setTime(d.getTime() + (expire_days * 24 * 60 * 60 * 1000));
    const expires = 'expires=' + d.toUTCString();
    document.cookie = `theme=${this.themeClassName}` + '; ' + expires + ';';
  }
}
