import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Router, NavigationStart, NavigationEnd } from '@angular/router';
import { filter } from 'rxjs/operators';
import { ObserableService } from '../obserable.service';

@Component({
  selector: 'app-top-menu',
  templateUrl: './top-menu.component.html',
  styleUrls: ['./top-menu.component.css']
})
export class TopMenuComponent implements OnInit {
  @Input() themeNameInput: string;
  @Output() themeNameOnput = new EventEmitter<string>();
  @Input() isLeftmenyOpenedInput: boolean;
  @Output() isLeftmenyOpenedOnput = new EventEmitter<boolean>();
  private title: string;

  constructor(private router: Router, private obsService: ObserableService) {
  }

  ngOnInit() {
    this.router.events.pipe(
      filter((event) => event instanceof NavigationEnd))
      .subscribe((event: NavigationEnd) => {
        switch (event.url) {
          case '/about-me':
            this.title = '基本資料';
            break;
          case '/skills':
            this.title = '技能';
            break;
          case '/artists':
            this.title = '網頁作品';
            break;
          case '/':
            this.title = '基本資料';
            break;
          default:
            this.title = 'Not found';
        }
      });
  }

  btnChangeThem() {
    if (this.themeNameInput === 'custom-theme-light') {
      this.themeNameOnput.emit('custom-theme-dark');
    } else {
      this.themeNameOnput.emit('custom-theme-light');
    }
  }

  btnIsLeftmenyOpened() {
    this.isLeftmenyOpenedOnput.emit(!this.isLeftmenyOpenedInput);
  }

}
